import Crypto from "y_crypto"
import socket from "../transport/Connect"
import helpers from "../helpers"

// we form an array with all the keys (and encryption and signatures)
let allEnctyptKeys = JSON.parse(localStorage.getItem("keysPair"))
let allSignKeys = JSON.parse(localStorage.getItem("signKeysPair"))
let formatedAllKeys = null
if(allEnctyptKeys && localStorage.getItem("User")) {
  formatedAllKeys = allEnctyptKeys.map(key => {
    let formatedEnctyptKey = {
      KeyId: key.id,
      UserId: JSON.parse(localStorage.getItem("User")).Id,
      PublicKey: key.public,
      PrivateKey: key.private,
      Lifetime: 99999,
      GenerationTime: 0,
      IsSign: false
    }
    return formatedEnctyptKey
  })
}

if(allSignKeys && localStorage.getItem("User")) {
  allSignKeys.forEach(key => {
    let formatedSignKey = {
      KeyId: key.id,
      UserId: JSON.parse(localStorage.getItem("User")).Id,
      PublicKey: key.public,
      PrivateKey: key.private,
      Lifetime: 99999,
      GenerationTime: 0,
      IsSign: true
    }
  
    formatedAllKeys.push(formatedSignKey)
  })
}

export function devicesKeysExchange(interlocutorPublicKey, requestId) {
  // encrypt and send keys
  const encoder = new TextEncoder()
  const masterPassword = encoder.encode('qwerty')
  let masterPasswordId = BigInt(1)
  const crypto = new Crypto(masterPassword, masterPasswordId)

  let mySignPrivateKey = JSON.parse(localStorage.getItem("signKeysPair"))[JSON.parse(localStorage.getItem("signKeysPair")).length-1]
  let symmetricKyeId = socket.makeId()
  crypto.setPublicEncryptKeyToSend(helpers.base64ToBuffer(interlocutorPublicKey))
  crypto.setPrivateSignKeyToSend(helpers.base64ToBuffer(mySignPrivateKey.private))
  crypto.getSymmetricKey(1, symmetricKyeId, 99999, ).then(symmetricKey => {
    crypto.setSymmetricKey(symmetricKey)

    // encrypt an array of all keys
    crypto.encryptSecretMsg(1, 1, 99999, encoder.encode(JSON.stringify(formatedAllKeys))).then(encryptedData => {
      // console.log(encryptedData)

      // we encrypt the symmetric key itself on which the array of all keys is encrypted
      crypto.encryptKeyMsg(1, 99999, symmetricKey).then(encryptedSymmetricKey => {
        // console.log(encryptedSymmetricKey)

        let clientResponse = {
          ResponseType: 1,
          Type: 9,
          EncryptedSymmetricKey: helpers.bufferToBase64(encryptedSymmetricKey),
          EncryptedData: helpers.bufferToBase64(encryptedData),
          PublicKey: mySignPrivateKey.public,
          RequestId: requestId
        }

        socket.send(clientResponse)
      }).catch(err => {
        // console.log(err)
      })
    }).catch(err => {
      // console.log(err)
    })
  }).catch(err => {
    // console.log(err)
  })
}

export function setKeysFromAnotherDevice(data){
  const encoder = new TextEncoder()
  const decoder = new TextDecoder()
  const masterPassword = encoder.encode('qwerty')
  let masterPasswordId = BigInt(1)
  const crypto = new Crypto(masterPassword, masterPasswordId)

  let myEncryptKey = JSON.parse(localStorage.getItem("keysPair"))[JSON.parse(localStorage.getItem("keysPair")).length-1]

  crypto.setPublicSignKeyToRecive(helpers.base64ToBuffer(data.PublicKey))
  crypto.setPrivateEncryptKeyToRecive(helpers.base64ToBuffer(myEncryptKey.private))

  crypto.decryptKeyMsg(helpers.base64ToBuffer(data.EncryptedSymmetricKey)).then(decryptSymmetricKey => {
    // console.log('симметричный расшифрован - ', decryptSymmetricKey)
    if(decryptSymmetricKey) {
      crypto.setSymmetricKey(decryptSymmetricKey.key)
      // console.log(helpers.base64ToBuffer(data.EncryptedKeys))
      crypto.decryptSecretMsg(helpers.base64ToBuffer(data.EncryptedKeys)).then(result => {
        // console.log('Ключи с другого устройства расшифрованы', decoder.decode(result.msg))
        if(result.msg) {
          let keysFromAnotherDevice = JSON.parse(decoder.decode(result.msg))
          // console.log('allMyKeys', formatedAllKeys)
          // console.log('keysFromAnotherDevice', keysFromAnotherDevice)

          let idAllMykeys = []
          if(formatedAllKeys){
            idAllMykeys = formatedAllKeys.map(key => {
              return key.KeyId
            })
          }

          keysFromAnotherDevice.forEach(key => {
            if(!idAllMykeys.includes(key.KeyId)){
              if(key.IsSign) {
                let allSignKeyPairs = JSON.parse(localStorage.getItem("signKeysPair")) || []
                let newKey = {}
                newKey.id = key.KeyId
                newKey.public = key.PublicKey
                newKey.private = key.PrivateKey
                allSignKeyPairs.push(newKey)
            
                localStorage.setItem("signKeysPair", JSON.stringify(allSignKeyPairs))
              } 
              else {
                let allEncryptKeyPairs = JSON.parse(localStorage.getItem("keysPair")) || []
                let newKey = {}
                newKey.id = key.KeyId
                newKey.public = key.PublicKey
                newKey.private = key.PrivateKey
                allEncryptKeyPairs.push(newKey)
            
                localStorage.setItem("keysPair", JSON.stringify(allEncryptKeyPairs))
              }
            }
          })
        }
      }).catch(err => {
        // console.log(err)
      })
    }
  }).catch(err => {
    // console.log(err)
  })
}